import Vue from 'vue'
import App from './App'
import router from './router'
import vuetify from './plugins/vuetify';
import axios from 'axios'
import store from './store'

Vue.config.productionTip = false

Vue.prototype.$axios = axios

let hostCpi = ''
let hostFiori = ''

if (window.location.href.toLowerCase().indexOf('fioridev') > 0)
  hostCpi =  "https://api-dev.apimanagement.br10.hana.ondemand.com/cpi-I40087"

if (window.location.href.toLowerCase().indexOf('brsaolsvfid01') > 0)
  hostCpi =  "https://api-dev.apimanagement.br10.hana.ondemand.com/cpi-I40087"

if (window.location.href.toLowerCase().indexOf('fiori.vo') > 0)
  hostCpi = "https://votorantim.apimanagement.br10.hana.ondemand.com:443/cpi-prd"

if (window.location.href.toLowerCase().indexOf('brsaolxvfip01.vo') > 0)
  hostCpi = "https://votorantim.apimanagement.br10.hana.ondemand.com:443/cpi-prd"

if (window.location.href.toLowerCase().indexOf('lwu9ep3mzz') > 0)
  hostCpi = "https://votorantim.apimanagement.br10.hana.ondemand.com:443/cpi-prd"

if (window.location.href.toLowerCase().indexOf('localhost') > 0)
{
  hostFiori = "https://api-dev.apimanagement.br10.hana.ondemand.com:443/v1/ZGWVSAHR_OPRH_SRV/"
  //hostFiori = "https://api-dev.apimanagement.br10.hana.ondemand.com:443/FIORIDEV2/ZGWVSAHR_OPRH_SRV/"
  hostCpi =  "https://api-dev.apimanagement.br10.hana.ondemand.com/cpi-I40087"
} 
  if (window.location.href.toLowerCase().indexOf('fioridev') > 0)
  //hostFiori = "/sap/opu/odata/sap/ZGWVSAHR_OPRH_SRV/ZET_VSAHR_BuscaEmpresaSet"
  hostFiori = "/sap/opu/odata/sap/ZGWVSAHR_OPRH_SRV/"

  if (window.location.href.toLowerCase().indexOf('fiori.vo') > 0)
  //hostFiori = "/sap/opu/odata/sap/ZGWVSAHR_OPRH_SRV/ZET_VSAHR_BuscaEmpresaSet"
  hostFiori = "/sap/opu/odata/sap/ZGWVSAHR_OPRH_SRV/"

if(hostCpi === '')
  hostCpi =  "https://votorantim.apimanagement.br10.hana.ondemand.com:443/cpi-prd"

//QAS hostCpi = "https://votorantimqas.apimanagement.br1.hana.ondemand.com:443/cpi008"
//https://votorantim.apimanagement.br10.hana.ondemand.com:443/cpi-prd/http/vsa/buscaCandidatosConcluidos


Vue.prototype.$endpoint = {cpi: hostCpi, fiori: hostFiori}

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  vuetify,
  store,
  template: '<App/>'
})



